.photo-tape__text {
  margin: 0 auto;
  text-align: center;
}

.photo-tape__text-container {
  margin-top: 45px;
  margin-bottom: 49px;
}

.photo-tape__text--top {
  width: 260px;
  font-size: 14px;
  line-height: 30px;
  font-weight: 500;
  color: @bg-navy;
}

.photo-tape__text--bottom {
  width: 280px;
  margin-top: 20px;
  margin-bottom: 60px;
  padding-right: 2px;
  font-size: 14px;
  line-height: 30px;
  font-weight: 500;
}

.photo-tape__text-nomobile {
  display: none;
}

.photo-tape__list {
  .list-decoration-none;
  display: flex;
  flex-direction: column;
}

.photo-tape__item {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.photo-tape__item-view {
  position: relative;
  width: 320px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.photo-tape__item-view::after {
  position: absolute;
  top: 0;
  left: 160px;
  content: "ПАНОРАМА ДНЯ";
  width: 140px;
  height: 36px;
  box-sizing: border-box;
  background-color: @pink;
  font-size: 12px;
  line-height: 24px;
  font-weight: 700;
  color: @white;
  text-align: center;
  padding-top: 6px;
}

.photo-tape__information-wrapper {
  position: relative;
  width: 280px;
  box-sizing: border-box;
  margin: 0 auto;
  margin-top: -10px;
  padding: 20px 20px;
  padding-left: 18px;
  padding-top: 15px;
  background-color: @gray;
}

.photo-tape__information-wrapper-view {
  padding-top: 15px;
}

.photo-tape__author {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  display: inline-block;
}

.photo-tape__time {
  margin: 0;
  padding: 0;
  display: inline-block;
  font-size: 12px;
  line-height: 24px;
  font-weight: 400;
  text-transform: uppercase;
  color: @gray-time;
}

.photo-tape__comment {
  margin: 0;
  margin-bottom: 10px;
  margin-top: -3px;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
}

.photo-tape__comment-drumset {
  margin-bottom: 12px;
}

.photo-tape__comment-window {
  margin-bottom: 11px;
}

.photo-tape__comment-fair {
  margin-bottom: 12px;
}

.photo-tape__comment-pov {
  margin-bottom: 12px;
}

.photo-tape__comment-wing {
  margin-bottom: 12px;
}

.photo-tape__likes {
  margin: 0;
  margin-left: 32px;
  margin-top: -1px;
  padding: 0;
  text-transform: uppercase;
  color: @pink;
  font-size: 12px;
  line-height: 24px;
  font-weight: 500;
}

.photo-tape__likes-svg {
  position: absolute;
  fill: @pink;
  bottom: 25px;
}

.photo-tape__likes-view {
  position: relative;
  margin: 0;
  margin-left: 32px;
  padding: 0;
  font-size: 12px;
  line-height: 24px;
  font-weight: 500;
  text-transform: uppercase;
  color: @pink;
}

@media (min-width: @tablet-width) {
  .photo-tape__text {
    width: 448px;
    margin-top: 90px;
    margin-bottom: 77px;
    padding-left: 0;
    font-size: 18px;
  }

  .photo-tape__text-container {
    display: none;
  }

  .photo-tape__text--bottom {
    margin-top: 45px;
  }

  .photo-tape__text-nomobile {
    display: block;
    margin-top: 92px;
    margin-bottom: 76px;
  }

  .photo-tape__list {
    flex-direction: row;
    flex-wrap: wrap;
    padding-left: 10px;
    padding-right: 10px;
  }

  .photo-tape__item {
    display: flex;
    flex-direction: column;
    width: 300px;
  }

  .photo-tape__image {
    width: 300px;
    height: 300px;
  }

  .photo-tape__item-view {
    /*width: 660px;*/
    width: 100%;
    margin-bottom: 50px;
  }

  .photo-tape__item-view::after {
    left: 500px;
    top: 57px;
  }

  .photo-tape__image-view {
    width: 100%;
    height: 509px;
  }

  .photo-tape__information-wrapper {
    position: relative;
    width: 300px;
    margin-top: -10px;
    padding-left: 28px;
    padding-top: 21px;
    min-height: 155px;
  }

  .photo-tape__information-wrapper-view {
    position: relative;
    width: 620px;
    min-height: 120px;
    margin: 0 auto;
    margin-top: -10px;
    box-sizing: border-box;
    padding-top: 21px;
    padding-bottom: 15px;
    padding-left: 28px;
  }

  .photo-tape__likes {
    position: absolute;
    bottom: 22px;
    left: -4px;
  }

  .photo-tape__likes-view {
    position: absolute;
    top: 26px;
    right: 58px;
  }

  .photo-tape__likes-view-svg {
    top: 30px;
    right: 29px;
  }

  .photo-tape__likes-svg {
    right: 32px;
    bottom: 28px;
  }
}

@media (min-width: @desktop-width) {
  .photo-tape__text {
    width: 580px;
    font-size: 18px;
    line-height: 30px;
    margin-top: 92px;
    margin-bottom: 88px;
    padding-left: 0;
  }

  .photo-tape__list {
    width: 940px;
    margin: 0 auto;
    justify-content: space-between;
  }

  .photo-tape__item {
    margin-bottom: 54px;
    margin-left: 0;
    margin-right: 0;
  }

  .photo-tape__image {
    width: 300px;
    height: 400px;
  }

  .photo-tape__item-view {
    width: 1200px;
    margin-bottom: 50px;
  }

  .photo-tape__item-view::after {
    left: 930px;
    top: 54px;
  }

  .photo-tape__image-view {
    width: 1200px;
    height: 506px;
  }

  .photo-tape__information-wrapper {
    padding-top: 20px;
    padding-left: 28px;
  }

  .photo-tape__information-wrapper-view {
    width: 940px;
    min-height: 95px;
    padding-top: 20px;
  }

  .photo-tape__likes {
    left: 0;
  }

  .photo-tape__likes-view {
    right: 65px;
  }

  .photo-tape__text--bottom {
    margin-top: 5px;
    margin-bottom: 93px;
  }
}
