.main-nav {
  position: relative;
  background-color: #283645;
  color: #ffffff;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
  font-weight: 700;
}
.main-nav__toggle {
  position: absolute;
  top: -65px;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 18px;
  left: -10px;
  width: 50px;
  height: 5px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 25px;
  right: 16px;
  width: 30px;
  height: 5px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.main-nav--opened .opacity-box {
  display: none;
}
.main-nav--nojs .opacity-box {
  display: none;
}
.main-nav--closed .opacity-box {
  position: absolute;
  top: -34px;
  width: 100%;
  min-height: 106px;
  margin-top: -76px;
  background-color: rgba(0, 0, 0, 0.3);
}
@media (max-width: 1199px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--opened .site-list__item {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: -81px;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    margin-left: 267px;
    padding-top: 4px;
    font-size: 18px;
    line-height: 18px;
    font-weight: 500;
    text-transform: none;
    background-color: transparent;
  }
  .main-nav__toggle {
    display: none;
  }
}
